.form {
    width: 320px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 0 auto;
}

.input{
   
        width: 254px;
            height: 27px;
            margin: 10px 10px;
}
.label{
    font-family: 'Montserrat', sans-serif;
        line-height: 1.2;
        font-weight: 600;
        font-size: 18px;
        color: #373737;
        margin-bottom: 20px;
}
.btn{
    margin: 0 auto;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 14px 28px;
        gap: 6px;
    
    
        width: 156px;
        height: 45px;
    
    
    
        border: none;
        box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
        border-radius: 10.3108px;
    
        font-family: 'Montserrat', sans-serif;
        line-height: 1.2;
        font-weight: 600;
        font-size: 18px;
        color: #373737;
        margin-bottom: 20px;
        margin-left: 63px;
}

.btn:hover {
    background-color: #d6d3d3;
    cursor: pointer;
}